<template>
    <div>
        <loading :active.sync="isLoading" background-color="black" color="white"
        :is-full-page="fullPage"></loading>
        <Navbar></Navbar>
        <div class="product-box">
            <div class="container h-100 pb-1rem">
                <ol class="breadcrumb pb-2rem">
                    <li class="breadcrumb-item"><a>所有商品</a></li>
                    <li class="breadcrumb-item"><a href="#">{{product.category}}</a></li>
                    <li class="breadcrumb-item active" aria-current="page">{{product.title}}</li>
                </ol>
                <div class="row ml-0 mr-0 h-80">
                  <div class="col-12 col-md-6 product-col">
                        <img class="product-img" :src="product.imageUrl" alt="">
                    </div>
                    <div class="col-12 col-md-6 h-80">
                        <div class="prodct-box-text">
                        <h2 class="product-box-title text-lyellow">{{product.title}}</h2>
                        <div class="text-left" v-if="product.price== product.origin_price">原價: {{product.price}}元</div>
                        <div class="text-left"><del class="h6" v-if="product.price!= product.origin_price">原價:{{ product.origin_price }} 元</del></div>
                        <div class="h4 text-left text-onsale" v-if="product.price !=product.origin_price">夏日特賣:優惠 {{ product.price }} 元</div>
                        <h3 class="text-right">總計</h3>
                        <h4 class="text-right text-price">{{one_product_sum}}</h4>
                        <div class="form-group">
                          <label for="num" class="text-left">數量</label>
                          <select name="num" class="form-control mb-3" id="buy_numberInput" v-model="product_buy_number">
                            <option value="1">訂購1{{product.unit}}</option>
                            <option value="2">訂購2{{product.unit}}</option>
                            <option value="3">訂購3{{product.unit}}</option>
                            <option value="4">訂購4{{product.unit}}</option>
                            <option value="5">訂購5{{product.unit}}</option>
                            <option value="6">訂購6{{product.unit}}</option>
                          </select>
                          <button class="btn btn-primary w-100" @click="addtoCart(product.id, product_buy_number,)">加入購物車木箱</button>
                        </div>
                        <div class="product-content text-left">
                          <h4 class="text-lyellow mb-2">商品介紹</h4>
                          <p>{{product.description}}</p>
                          <h4 class="text-lyellow">補充說明</h4>
                          <p>{{product.content}}</p>
                        </div>
                      </div>
                  </div>
                </div>
            </div>
        </div>
    <Footer></Footer>
    </div>
</template>
<script>
import Navbar from '../navbar.vue'
import Footer from '../footer.vue'
export default {
  /*
  props: {
    id: this.$route.params.id
  },
  */
  data: function () {
    return {
      product: {},
      product_buy_number: 1,
      isLoading: false,
      fullPage: true,
      status: {
        loadingItem: ''
      }
    }
  },
  components: {
    Navbar,
    Footer
  },
  methods: {
    getProduct () {
      const vm = this
      vm.isLoading = true
      const url = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/products`
      this.$http.get(url).then(function (response) {
        const pm = response.data.products
        pm.forEach(function (item, key) {
          if (item.id === vm.$route.params.id) {
            vm.product = item
            vm.isLoading = false
            console.log(vm.product)
          }
        })
      })
    },
    addtoCart (id, qty = 1) {
      const vm = this
      const url = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/cart`
      vm.status.loadingItem = id
      const cart = {
        product_id: id,
        qty
      }
      this.$http.post(url, { data: cart }).then((response) => {
        console.log(response)
        vm.status.loadingItem = ''
        vm.getProduct()
      })
    }
  },
  created () {
    this.getProduct()
  },
  computed: {
    one_product_sum: function () {
      let num = 1
      const re = /^[0-9].?[0-9]*/
      for (let i = 0; i < this.product_buy_number.length; i++) {
        if (re.test(this.product_buy_number[i])) {
          // console.log(this.product_buy_number[i])
          num = parseInt(this.product_buy_number[i], 10)
        }
      }
      // console.log(num)
      return this.product.price * num
    }
  },
  mounted () {
  }
}
</script>
<style scoped lang="scss">
.bg-nav{
    background-color: #343A40;
    position: relative;
    opacity: 1;
}
.navbar .nav-item .nav-link{
    color: rgba(0, 0, 0, 0.25);
}
.navbar .nav-item .nav-link:hover{
    color: white;
}
.product-box{
    height: 652px;
    background-color: #002055;
    background-image: radial-gradient(ellipse 50% 70% at 50% 0,#002c74 20%,#000000);
    .container{
        padding-left: 15px;
        padding-right: 15px;
        height:680px;
    }
    .product-img{
        padding-left: 1.5rem;
        padding-right: 1.5rem;
        width: 80%;
        height: 522px;
    }
    .product-box-text{
        padding-left: 40px;
    }
    .product-box-title{
        text-align: left;
    }
}
.breadcrumb{
    background-color: transparent;
    padding-left: 16px;
    padding-right: 16px;
}
a{
  color: white;
}
.breadcrumb .breadcrumb-item .active{
  color: #df6916;
}
</style>
