<template>
    <div class="bg-black ptl-100">
    <div class="row justify-content-center" @submit.prevent="createOrder">
    <form class="col-md-6">
      <div class="form-group text-white">
        <label for="useremail">Email</label>
        <input type="email" class="form-control" name="email" id="useremail"
         v-validate="'required|email'" v-model="form.user.email" placeholder="請輸入 Email" required>
        <span class="text-danger" v-if="errors.has('email')">{{ errors.first('email') }}</span>
      </div>
      <div class="form-group">
        <label for="username">收件人姓名</label>
        <input type="text" class="form-control" name="name" id="username"
        :class="{'is-invalid': errors.has('name')}"
          v-model="form.user.name" v-validate="'required'" placeholder="輸入姓名">
        <span class="text-danger" v-if="errors.has('name')">姓名必須輸入</span>
      </div>
      <div class="form-group">
        <label for="usertel">收件人電話</label>
        <input type="tel" class="form-control" name="cellphone" id="usertel" :class="{'is-invalid': errors.has('cellphone')}" v-validate="'required'" v-model="form.user.tel" placeholder="請輸入電話">
        <span class="text-danger" v-if="errors.has('cellphone')">電話必須輸入</span>
      </div>
      <div class="form-group">
        <label for="useraddress">收件人地址</label>
        <input type="text" class="form-control" name="address" id="useraddress" v-model="form.user.address"
        :class="{'is-invalid': errors.has('address')}" v-validate="'required'"
          placeholder="請輸入地址">
        <span class="text-danger" v-if="errors.has('address')" >地址欄位不得留空</span>
      </div>
      <div class="form-group">
        <label for="comment">留言</label>
        <textarea name="" id="comment" class="form-control" cols="30" rows="10" v-model="form.message"></textarea>
      </div>
      <div class="text-right">
        <button class="btn btn-danger">送出訂單</button>
      </div>
    </form>
  </div>
  </div>
</template>
<script>
export default {
  data () {
    return {
      form: {
        user: {
          name: '',
          email: '',
          tel: '',
          address: ''
        }
      }
    }
  },
  methods: {
    createOrder () {
      const vm = this
      const url = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/order`
      const order = vm.form
      this.$validator.validate().then((result) => {
        if (result) {
          this.$http.post(url, { data: order }).then((response) => {
            console.log('訂單已建立', response)
            if (response.data.success) {
              vm.$router.push(`/checkout/${response.data.orderId}`)
            }
            vm.isLoading = false
          })
        } else {
          console.log('欄位不完整')
        }
      })
    }
  }
}
</script>
<style scoped lang="scss">
.ptl-100{
    padding-top: 100px;
    padding-bottom: 100px;
}
.bg-black{
  background-color: #002055;
  background-image: radial-gradient(ellipse 50% 70% at 50% 0,#002c74 20%,#000000);
}
.text-white{
  color: white;
}
</style>
