<template>
    <div>
        <Navbar :cart="cart"></Navbar>
    <loading :active.sync="isLoading" background-color="black" color="white"
    :is-full-page="fullPage"></loading>
      <div class="toast" style="position: absolute; top: 100; right: 0;" id="element">
        <div class="toast-header text-white">
          <!--img src="..." class="rounded mr-2" alt="..."-->
          <strong class="mr-auto">通知訊息</strong>
          <button type="button" class="ml-2 mb-1 close" data-dismiss="toast" aria-label="Close">
            <span aria-hidden="true">&times;</span>
          </button>
        </div>
        <div class="toast-body">
          {{noticemessage}}}
        </div>
      </div>
    <div class="product-list">
    <div class="container main-content">
        <div class="row">
            <div class="col-3 product-group">
                <div class="list-group" id="list-tab" role="tablist">
                <a class="list-group-item list-group-item-action active list-header" @click="ProductFilter('全部商品')" id="list-all" data-toggle="list" href="#content-all" role="tab" aria-controls="all">全部商品
                  <span class="badge badge-primary badge-pill float-right">{{product_all_len}}</span>
                </a>
                <a class="list-group-item list-group-item-action" id="list-profile-list" @click="ProductFilter('套裝')" data-toggle="list" href="#content-suits" role="tab" aria-controls="suits">套裝
                  <span class="badge badge-primary badge-pill float-right">{{products_suits_len}}</span></a>
                <a class="list-group-item list-group-item-action" id="list-profile-list" @click="ProductFilter('武器')" data-toggle="list" href="#content-weapons" role="tab" aria-controls="weapon">武器
                  <span class="badge badge-primary badge-pill float-right">{{products_weapons_len}}</span></a>
                <a class="list-group-item list-group-item-action" id="list-profile-list" @click="ProductFilter('防器')" data-toggle="list" href="#content-defenses" role="tab" aria-controls="defenses">防器
                  <span class="badge badge-primary badge-pill float-right">{{products_defenses_len}}</span></a>
                <a class="list-group-item list-group-item-action" id="list-profile-list" @click="ProductFilter('配件')" data-toggle="list" href="#content-acessorys" role="tab" aria-controls="acessory">配件
                  <span class="badge badge-primary badge-pill float-right">{{products_acessorys_len}}</span></a>
                <a class="list-group-item list-group-item-action" id="list-settings-list" @click="ProductFilter('補品')" data-toggle="list" href="#content-herbs" role="tab" aria-controls="herbs">補品
                  <span class="badge badge-primary badge-pill float-right">{{products_herbs_len}}</span></a>
                </div>
            </div>
            <div class="col-9 product-wall">
                    <div class="d-flex justify-content-between mb-4">
                        <h2 class="text-lyellow">{{product_title}}</h2>
                        <form class="form-inline my-2 my-lg-0">
                            <input class="form-control mr-sm-2" type="search" placeholder="Search" aria-label="Search" v-model="search_txt">
                            <button class="btn btn-outline-success my-2 my-sm-0" type="submit" @click="SearchProduct(search_txt)">Search</button>
                        </form>
                    </div>
                    <div class="tab-content" id="nav-tabContent">
                        <div class="tab-pane fade show active" id="content-all" role="tabpanel" aria-labelledby="list-all">
                            <div class="container">
                                <div class="row">
                                    <div class="col-md-4 mb-4 pl-0" v-for="item in products_Search" :key="item.id">
                                        <div class="card border-0 shadow-sm h-100">
                                            <div style="height: 300px; background-size: cover; background-position: center"
                                                :style="{backgroundImage: `url(${item.imageUrl})`}">
                                            </div>
                                            <div class="card-body bg-shadowdark">
                                                <h5 class="card-title d-flex align-items-center">
                                                <a href="#" class="product-text text-lyellow mr-auto" >{{ item.title }}</a>
                                                <span class="badge badge-secondary ml-2">{{ item.category }}</span>
                                                </h5>
                                                <div class="d-flex flex-column justify-content-between align-items-baseline text-white">
                                                    <div class="h6" v-if="item.price == item.origin_price">售價:{{ item.price }} 元</div>
                                                    <del class="h6" v-if="item.price!= item.origin_price">原價:{{ item.origin_price }} 元</del>
                                                    <div class="h5" v-if="item.price !=item.origin_price">夏日特賣:優惠 {{ item.price }} 元</div>
                                                </div>
                                            </div>
                                            <div class="card-footer d-flex text-lyellow bg-shadowdark">
                                                <router-link :to="{ name:'Product_detail',params: { id:item.id }}">
                                                  <button type="button" class="btn btn-outline-secondary btn-sm">
                                                  <i class="fas  fa-spin"></i>查看商品資訊
                                                </button>
                                                </router-link>
                                                <button type="button" class="btn btn-outline-danger btn-sm ml-auto" @click="addtoCart(item.id)">
                                                <i class="fas  fa-spin"></i>加到購物車
                                                </button>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div class="tab-pane fade" id="content-suits" role="tabpanel" aria-labelledby="list-profile-list">
                            <Card :productSearch="products_Search"></Card>
                        </div>
                        <div class="tab-pane fade" id="content-weapons" role="tabpanel" aria-labelledby="list-profile-list">
                            <Card :productSearch="products_Search"></Card>
                        </div>
                        <div class="tab-pane fade" id="content-defenses" role="tabpanel" aria-labelledby="list-profile-list">
                            <Card :productSearch="products_Search"></Card>
                        </div>
                        <div class="tab-pane fade" id="content-acessorys" role="tabpanel" aria-labelledby="list-profiole-list">
                            <Card :productSearch="products_Search"></Card>
                        </div>
                        <div class="tab-pane fade" id="content-herbs" role="tabpanel" aria-labelledby="list-settings-list">
                            <Card :productSearch="products_Search"></Card>
                        </div>
                    </div>
                 <Pagination :pages="pagination" @emitPages="getProducts"></Pagination>
            </div>
        </div>
    </div>
    </div>
        <Footer></Footer>
    </div>
</template>
<script>
import $ from 'jquery'
import Navbar from '../navbar.vue'
import Footer from '../footer.vue'
import Card from './card.vue'
import Pagination from './pagination.vue'
const options = { animation: true, autohide: true, delay: 500 }
export default {
  data () {
    return {
      status: {
        loadingItem: ''
      },
      cart: {},
      products: [],
      products_Search: [],
      product_all_len: '',
      product_weapons_len: '',
      proudct_defense_len: '',
      product_title: '全部商品',
      prouduct_accessorys_len: '',
      proudct_herbs_len: '',
      pagination: {},
      search_txt: '',
      isLoading: true,
      fullPage: true,
      coupon_code: '',
      noticemessage: ''
    }
  },
  methods: {
    getProducts () {
      const vm = this
      vm.loading = true
      const url = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/products`
      this.$http.get(url).then(function (response) {
        //  console.log(response)
        vm.products = response.data.products
        vm.products_Search = vm.products
        vm.isLoading = false
        vm.pagination = response.data.pagination
      })
    },
    ProductFilter (category) {
      var vm = this
      this.products_Search = []
      vm.loading = true
      vm.product_title = category
      if (category === '全部商品') {
        vm.products_Search = vm.products
      } else {
        for (let i = 0; i < this.products.length; i++) {
          if (this.products[i].category === category) {
            vm.isLoading = false
            this.products_Search.push(this.products[i])
          }
        }
      }
    },
    ProductChangeName (name) {
      var vm = this
      vm.product_title = name
    },
    SearchProduct (text) {
      var vm = this
      console.log(text + '12345')
      var newArr = []
      var find = false
      const searchlen = this.products_Search.length
      for (let i = 0; i < searchlen; i++) {
        if (vm.products_Search[i].title.indexOf(text) !== -1) {
          find = true
        }
        if (find) {
          newArr.push(this.products_Search[i])
        }
        find = false
      }
      vm.products_Search = newArr
    },
    addtoCart (id, qty = 1) {
      const vm = this
      const url = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/cart`
      vm.status.loadingItem = id
      const cart = {
        product_id: id,
        qty
      }
      this.$http.post(url, { data: cart }).then((response) => {
        console.log(response)
        vm.status.loadingItem = ''
        vm.getCart()
        $('#productModal').modal('show')
      })
    },
    getCart () {
      const vm = this
      const url = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/cart`
      vm.isLoading = true
      this.$http.get(url).then((response) => {
        // vm.product = response.data.product
        vm.cart = response.data.data
        $('#productModal').modal('hide')
        vm.$bus.$emit('Navbar', vm.cart)
        console.log(response)
        $('#element').toast(options)
        $('#element').toast('show')
        vm.isLoading = false
      })
    }
  },
  computed: {
    products_suits_len: function () {
      let total = 0
      for (let i = 0; i < this.products.length; i++) {
        if (this.products[i].category === '套裝') { total = total + 1 }
      }
      return total
    },
    products_weapons_len: function () {
      let total = 0
      for (let i = 0; i < this.products.length; i++) {
        if (this.products[i].category === '武器') { total = total + 1 }
      }
      return total
    },
    products_defenses_len: function () {
      let total = 0
      for (let i = 0; i < this.products.length; i++) {
        if (this.products[i].category === '防器') { total = total + 1 }
      }
      return total
    },
    products_acessorys_len: function () {
      let total = 0
      for (let i = 0; i < this.products.length; i++) {
        if (this.products[i].category === '配件') { total = total + 1 }
      }
      return total
    },
    products_herbs_len: function () {
      let total = 0
      for (let i = 0; i < this.products.length; i++) {
        if (this.products[i].category === '補品') { total = total + 1 }
      }
      return total
    }
  },
  components: {
    Pagination,
    Card,
    Navbar,
    Footer
  },
  created () {
    this.getProducts()
    this.getCart()
  },
  updated () {
    // 讓我們可以知道組件有被更新
    console.log('view updated')
  },
  watch: {
    products: function () {
      this.product_all_len = this.products.length
    }
  }
}
</script>
<style scoped lang="scss">

div .bg-nav{
    background-color: #343A40;
    position: relative;
    opacity: 1;
}
.navbar .nav-item .nav-link{
    color: rgba(0, 0, 0, 0.25);
}
.navbar .nav-item .nav-link:hover{
    color: white;
}
    .text-hot{
      color: #FF5809;
    }
    .product-list{
        background-color: #002055;
        background-image: radial-gradient(ellipse 50% 70% at 50% 0,#002c74 20%,#000000);
        position: sticky;
        left: 0;
        top: 110;
        z-index:900;
        .product-group{
            .active{
            background-color: #1b1e21;
            color: white;
            }
        }
        .card-body a:hover{
            color: white;
        }
    }
    .main-content{
        padding-top: 4rem;
        padding-bottom: 4rem;
    }
    .product-text{
        font-size: 1.5rem;
        font-family: "Roboto", sans-serif;
    }
</style>
