<script>
// import ProductLobby from './mainpages/productlobby.vue'
export default {
  props: ['cart'],
  data () {
    return {
      isLoading: false,
      isSuccess: false
    }
  },
  methods: {
    getCart () {
      const vm = this
      const url = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/cart`
      vm.isLoading = true
      vm.isSuccess = false
      this.$http.get(url).then((response) => {
        // vm.product = response.data.product
        vm.cart = response.data.data
        vm.isLoading = false
        vm.isSuccess = true
      })
    }
  },
  created () {
    this.getCart()
  }
