import Vue from 'vue'
import VueRouter from 'vue-router'
// import Home from '../views/Home.vue'
import Dashboard from '@/components/Dashboard.vue'
import Login from '@/components/pages/Login.vue'
import Products from '@/components/pages/Products.vue'
import Coupons from '@/components/pages/Coupons.vue'
import Orders from '@/components/pages/Orders.vue'
import CustomerOrder from '@/components/pages/CustomerOrders.vue'
import CustomerCheckout from '@/components/pages/CustomerCheckout.vue'
import index from '@/components/store/index.vue'
import home from '@/components/store/home.vue'
import ProductDetail from '@/components/store/mainpages/product_detail'
import ProductLobby from '@/components/store/mainpages/productlobby'
import Cart from '@/components/store/mainpages/cart'
import Form from '@/components/store/mainpages/form'
import Checkout from '@/components/store/mainpages/checkout'
Vue.use(VueRouter)
const routes = [
  {
    path: '/',
    name: '',
    component: index,
    children: [
      {
        path: '',
        name: 'Home',
        component: home
      },
      {
        path: '/cart',
        name: 'Cart',
        component: Cart
      },
      {
        path: '/form',
        name: 'Form',
        component: Form
      },
      {
        path: '/checkout/:orderId',
        name: 'Checkout',
        component: Checkout
      }
    ]
  },
  {
    path: '/productlobby',
    name: 'ProductLobby',
    component: ProductLobby
  },
  {
    path: '/product_detali/:id',
    name: 'Product_detail',
    component: ProductDetail,
    meta: {
      title: route => `{{ product_detail }}/${route.params.id}`,
      notCache: true
    }
  },
  {
    path: '*',
    redirect: '/'
  },
  {
    path: '/login',
    name: 'Login',
    component: Login
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    // component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
  },
  {
    path: '/admin',
    name: 'Dashboard',
    component: Dashboard,
    children: [
      {
        path: 'products',
        name: 'Products',
        component: Products,
        meta: { requiresAuth: true }
      },
      {
        path: 'coupons',
        name: 'Coupons',
        component: Coupons,
        meta: { requiresAuth: true }
      },
      {
        path: 'orders',
        name: 'Orders',
        component: Orders,
        meta: { requiresAuth: true }
      },
      {
        path: 'customer_order',
        name: 'CustomerOrder',
        component: CustomerOrder
      },
      {
        path: 'customer_checkout/:orderId',
        name: 'CustomerCheckout',
        component: CustomerCheckout
      }
    ]
  }
]
const router = new VueRouter({
  linkActiveClass: 'active',
  routes
})
export default router
