<template>
  <div>
  <Navbar></Navbar>
  <div class="bg-black">
    <loading :active.sync="isLoading" background-color="black" color="white"></loading>
    <div class="cart-list container">
        <div class="row justify-content-center mx-auto">
        <div class="col-md-6">
        <table class="table">
        <thead class="text-white">
            <th>品名</th>
            <th>數量</th>
            <th>單價</th>
            <th>金額</th>
        </thead>
            <tbody>
            <tr v-for="item in cart.carts" :key="item.id">
                <td class="aling-middle">
                <button type="button" class="btn btn-outline-danger btn-sm" @click="removeCartItem(item.id)">
                <i class="far fa-trash-alt"></i>
                </button>
                </td>
                <td class="align-middle text-white">{{ item.product.title }}
                <div class="text-success" v-if="item.coupon">
                已套用優惠券
                </div>
                </td>
                <td class="align-middle text-white">{{ item.qty }}{{ item.product.unit }}</td>
                <td class="align-middle text-right text-primary">{{ item.final_total }}</td>
            </tr>
            </tbody>
            <tfoot>
            <tr class="text-danger">
                <td colspan="2" class="text-right">總計</td>
                <td class="text-right">{{ cart.total }}</td>
                <td><router-link :to="{ name:'Form'}"><button class="btn btn-danger">前去結帳</button></router-link></td>
            </tr>
            <tr class="text-danger" v-if="cart.final_total != cart.total">
                <td colspan="3" class="text-right text-success">折扣價</td>
                <td>{{cart.final_total}}</td>
                <td><router-link :to="{ name:'Form'}"><button class="btn btn-danger">前去結帳</button></router-link></td>
            </tr>
        </tfoot>
        </table>
        <div class="input-group mb-3 input-group-sm">
        <input type="text" class="form-control" v-model="coupon_code" placeholder="請輸入優惠碼">
            <div class="input-group-append">
            <button class="btn btn-outline-secondary" type="button" @click="addCouponCode">
            套用優惠碼
            </button>
            </div>
        </div>
        </div>
    </div>
  </div>
</div>
<Footer></Footer>
</div>
</template>
<script>
import Navbar from '../navbar.vue'
import Footer from '../footer.vue'

export default {
  data () {
    return {
      status: {
        loadingItem: ''
      },
      cart: [],
      pagination: {},
      search_txt: '',
      isLoading: true,
      fullPage: true,
      coupon_code: ''
    }
  },
  methods: {
    getCart () {
      const vm = this
      const url = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/cart`
      vm.isLoading = true
      this.$http.get(url).then((response) => {
        // vm.product = response.data.product
        vm.cart = response.data.data
        vm.isLoading = false
      })
    },
    removeCartItem (id) {
      const vm = this
      const url = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/cart/${id}`
      vm.isLoading = true
      this.$http.delete(url).then((response) => {
        // vm.product = response.data.product
        vm.getCart()
      })
    },
    addCouponCode () {
      const vm = this
      const url = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/coupon`
      const coupon = {
        code: vm.coupon_code
      }
      vm.isLoading = true
      this.$http.post(url, { data: coupon }).then((response) => {
        vm.getCart()
        console.log(response)
        vm.isLoading = false
      })
    }
  },
  components: {
    Navbar,
    Footer
  },
  created () {
    this.getCart()
  }
}
</script>
<style scoped lang="scss">
.bg-black{
  background-color: #002055;
  background-image: radial-gradient(ellipse 50% 70% at 50% 0,#002c74 20%,#000000);
}
.text-white{
  color: white;
}
.cart-list{
    padding-top:150px;
    padding-bottom:150px;
}
</style>
