  <div>
    <router-view></router-view>
  </div>
</template>
<script>
function forbidRightMouse (evt) {
  if (evt) {
    evt.preventDefault()
  } else { // dom2的事件模型，主要針對firefox/sofari
    window.event.returnValue = false // IE的事件模型
  }
}
document.oncontextmenu = forbidRightMouse// 防止遊覽者使用滑鼠右鍵
</script>
