<template>
  <div class="bg-black ptl-100">
    <div class="modal" tabindex="-1" role="dialog" id="checkoutmodal">
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-body text-black">
            <p>已付款成功</p>
          </div>
          <div class="modal-footer">
            <a href="/"><button type="button" class="btn btn-primary">跳轉回首頁</button></a>
          </div>
        </div>
      </div>
    </div>
  <div class="row justify-content-center">
    <form class="col-md-6" @submit.prevent="payOrder">
      <table class="table">
        <thead class="text-white">
          <th>品名</th>
          <th>數量</th>
          <th>單價</th>
        </thead>
        <tbody>
          <tr v-for="item in order.products" :key="item.id" class="text-white">
            <td class="align-middle">{{ item.product.title }}</td>
            <td class="align-middle">{{ item.qty }}/{{ item.product.unit }}</td>
            <td class="align-middle text-right">{{ item.final_total }}</td>
          </tr>
        </tbody>
        <tfoot>
          <tr class="text-primary">
            <td colspan="2" class="text-right">總計</td>
            <td class="text-right">{{ order.total }}</td>
          </tr>
        </tfoot>
      </table>

      <table class="table">
        <tbody class="text-white">
          <tr>
            <th width="100">Email</th>
            <td>{{ order.user.email }}</td>
          </tr>
          <tr>
            <th>姓名</th>
            <td>{{ order.user.name }}</td>
          </tr>
          <tr>
            <th>收件人電話</th>
            <td>{{ order.user.tel }}</td>
          </tr>
          <tr>
            <th>收件人地址</th>
            <td>{{ order.user.address }}</td>
          </tr>
          <tr>
            <th>付款狀態</th>
            <td>
              <span class="text-danger" v-if="!order.is_paid">尚未付款</span>
              <span v-else class="text-success">付款完成</span>
            </td>
          </tr>
        </tbody>
      </table>
      <div class="text-right text-white" v-if="order.is_paid === false">
        <button class="btn btn-danger" data-toggle="modal" data-target="#checkoutmodal">確認付款去</button>
      </div>
    </form>
  </div>
  </div>
</template>

<script>
import $ from 'jquery'
export default {
  data () {
    return {
      order: {
        user: {}
      },
      orderId: ''
    }
  },
  methods: {
    getOrder () {
      const vm = this
      const url = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/order/${vm.orderId}`
      vm.isLoading = true
      this.$http.get(url).then((response) => {
        vm.order = response.data.order
        console.log(response)
        vm.isLoading = false
      })
    },
    payOrder () {
      const vm = this
      const url = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/pay/${vm.orderId}`
      vm.isLoading = true
      this.$http.post(url).then((response) => {
        console.log(response)
        if (response.data.success) {
          vm.getOrder()
          $('#checkoutmodal').modal('show')
        }
        vm.isLoading = false
      })
    }
  },
  created () {
    this.orderId = this.$route.params.orderId
    this.getOrder()
    console.log(this.orderId)
  }
}
</script>
<style scoped lang="scss">
.ptl-100{
    padding-top: 100px;
    padding-bottom: 100px;
}
.bg-black{
  background-color: #002055;
  background-image: radial-gradient(ellipse 50% 70% at 50% 0,#002c74 20%,#000000);
}
.modal-dialog{
  width: 250px;
}
.text-white{
  color: white;
}
.text-black{
  color: black;
}
</style>
